<template>
  <div class="empty-response">
    <div class="grey-text center-align">
      <i class="material-icons">{{icon}}</i>
      <h5>
        {{text}}
      </h5>
    </div>
  </div>
</template>
<script>
export default {
  name: 'empty-response',
  data: () => ({

  }),
  props: {
    icon: {
      type: String,
      default: 'not_interested'
    },
    text: {
      type: String,
      default: 'No hay elementos para mostrar'
    }
  }
}
</script>
<style scoped>
  .material-icons{
    font-size: 120px
  }
  .empty-response{
    margin-top: 30px
  }
</style>



