<template>
  <div class="device-card">
    <div class="card row">
      <div class="col s12">
        <h5>{{device.name}}</h5>
      </div>
      <vue-chart :height="chartHeight" type="line" :data="chartData" :options="chartOptions"></vue-chart>
      <div class="card-action">
        <router-link :to="{ name: 'Dispositivo', params: { id: device._id}}">
          Ver más información
        </router-link>
      </div>
    </div>
  </div>
</template>
<script>
import VueChart from 'vue-chart-js'
export default {
  name: 'device-card',
  data: () => ({
    chartHeight: 200,
    chartData: {
      labels: ['10:00', '10:30', '11:00', '11:30', '12:00', '12:30'],
      datasets: [
        {
          label: 'Purple',
          data: [
            (Math.floor(Math.random() * 25)),
            (Math.floor(Math.random() * 25)),
            (Math.floor(Math.random() * 25)),
            (Math.floor(Math.random() * 25)),
            (Math.floor(Math.random() * 25)),
            (Math.floor(Math.random() * 25))
          ],
          backgroundColor: [
            'rgba(74, 20, 140, 0.2)'
          ],
          borderColor: [
            '#4a148c'
          ],
          borderWidth: 1,
          pointHoverBackgroundColor: '#4a148c',
          spanGaps: true,
          fill: false
        }
      ]
    },
    chartOptions: {
      showLines: true,
      scales: {
        yAxes: [{
          stacked: false
        }]
      }
    }
  }),
  methods: {
    getRandomColor () {
      var list = [
        '74, 20, 140',
        '104, 159, 56'
      ]
      return list[Math.floor(Math.random() * list.length - 1) + 1]
    }
  },
  props: {
    device: {
      type: Object,
      required: true
    }
  },
  components: {
    VueChart
  }
}
</script>
<style scoped>
</style>



