<template>
  <div class="device">
    <h5>{{device.name}}</h5>
    <ul v-if="device !== null">
      <li v-for="log in device.log">
        <template v-for="(val, key) in log">
          <template v-if="!hideKeys.includes(key)">
            {{key}} -- {{val}}
          </template>
        </template>
      </li>
    </ul>
  </div>
</template>
<script>
import axios from 'axios'
export default {
  name: 'device',
  data: () => ({
    device: null,
    hideKeys: [
      '_id',
      'device_id',
      'updated_at'
    ]
  }),
  mounted () {
    var scope = this
    axios.get(scope.$store.state.wss + 'device/' + scope.$route.params.id + '?token=' + scope.$store.state.token)
    .then(function (response) {
      console.log(response)
      scope.device = response.data
    })
    .catch(function (error) {
      console.log(error)
    })
  }
}
</script>
<style scoped>
</style>



