<template>
  <div class="modal-component">
    <div :id="id" class="modal">
      <div class="modal-content">
        <h4>
          {{title}}
        </h4>
        <p>
          {{body}}
        </p>
        <fast-form
          v-if="needValues != null"
          :needValues="needValues"
          :clearTrigger="clearTrigger"
          v-on:updateData="takeFormData"
        >
        </fast-form>
      </div>
      <div class="modal-footer">
        <button v-on:click="ok()" class="modal-action modal-close waves-effect waves-green btn-flat">
          {{okText}}
        </button>
        <button class="modal-action modal-close waves-effect waves-red btn-flat">
          {{cancelText}}
        </button>
      </div>
    </div>
  </div>
</template>
<script>
import FastForm from '@/components/UIcomponents/FastForm'
var $ = require('jquery')
export default {
  name: 'modal-component',
  data: () => ({
    id: '',
    data: null,
    clearTrigger: false
  }),
  methods: {
    open () {
      $('#' + this.id).modal('open')
    },
    clear () {
      this.clearTrigger = !this.clearTrigger
    },
    ok () {
      this.okCallback(this.data)
      this.clear()
    },
    takeFormData (data) {
      this.data = data
    },
    makeid () {
      var text = ''
      var possible = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789'

      for (var i = 0; i < 15; i++) {
        text += possible.charAt(Math.floor(Math.random() * possible.length))
      }
      return text
    }
  },
  props: {
    title: {
      type: String,
      default: 'Información'
    },
    body: {
      type: String
    },
    cancelText: {
      type: String,
      default: 'Cancelar'
    },
    okText: {
      type: String,
      default: 'Ok'
    },
    okCallback: {
      type: Function,
      default: function () {
        console.log('Default callback')
      }
    },
    toggle: {
      type: Boolean
    },
    needValues: {
      type: Array
    }
  },
  watch: {
    toggle: function () {
      this.open()
    }
  },
  mounted () {
    this.id = this.makeid()
    $(document).ready(function () {
      $('.modal').modal({
        dismissible: false
      })
    })
  },
  components: {
    FastForm
  }
}
</script>
<style scoped>
</style>



