<template>
  <div id="place-card">
    <div class="card horizontal">
      <div class="card-image">
        <span class="icon-card light-green darken-2">
          <i class="material-icons">place</i>
        </span>
      </div>
      <div class="card-stacked">
        <button class='dropdown-button btn-floating grey lighten-1' style="position: absolute; right: 10px; top: 10px;" :data-activates='id'>
          <i class="material-icons">
            more_horiz
          </i>
        </button>
        <!-- Dropdown Structure -->
        <ul :id='id' class='dropdown-content'>
          <li>
            <a class="clickable light-green-text text-darken-2">
              Editar
            </a>
          </li>
          <li>
            <a class="clickable light-green-text text-darken-2">
              Eliminar
            </a>
          </li>
        </ul>
        <div class="card-content">
          <span class="card-title">
            {{name}}
          </span>
          <p>
            {{description}}
          </p>
        </div>
        <div class="card-action">
          <router-link :to="{ name: 'Lugar', params: { name: _id}}">
            Ver mas
          </router-link>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
var $ = require('jquery')
export default {
  name: 'place-card',
  data: () => ({
    id: null
  }),
  props: {
    name: {
      trype: String,
      required: true
    },
    _id: {
      trype: String,
      required: true
    },
    description: {
      trype: String,
      required: true
    }
  },
  methods: {
    makeid () {
      var text = ''
      var possible = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789'

      for (var i = 0; i < 15; i++) {
        text += possible.charAt(Math.floor(Math.random() * possible.length))
      }
      return text
    }
  },
  mounted () {
    $(document).ready(function () {
      $('.dropdown-button').dropdown(
        {
          inDuration: 300,
          outDuration: 225,
          constrainWidth: false, // Does not change width of dropdown to that of the activator
          gutter: 0, // Spacing from edge
          belowOrigin: true, // Displays dropdown below the button
          alignment: 'left', // Displays dropdown with edge aligned to the left of button
          stopPropagation: false // Stops event propagation
        }
      )
    })
    this.id = this.makeid()
  }
}
</script>
<style scoped>
 .icon-card{
    width: 120px;
    height: 190px;
    display: block;
    position: relative;
        overflow: hidden;
  }

  .icon-card::after {
    content: "";
    opacity: 0.2;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    position: absolute;
    z-index: 0;
    background-image: url(../../assets/img/map.png);
    background-repeat: repeat;
  }

  .icon-card>i{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: white;
    font-size: 90px;
    z-index: 1;
  }
</style>



