<template>
  <div class="fast-from">
    <form>
      <div class="row">
        <template v-for="input in needValues">
          <div class="input-field col s12" v-if="[
            'text',
            'email',
            'password',
            'textarea'
          ].includes(input.type)">
          <template v-if="input.type == 'text'">
            <input type="text" v-model="values[input.name]">
          </template>
          <template v-if="input.type == 'email'">
            <input type="email" v-model="values[input.name]">
          </template>
          <template v-if="input.type == 'password'">
            <input type="password" v-model="values[input.name]">
          </template>
            <textarea v-if="input.type == 'textarea'" class="materialize-textarea"></textarea>
            <label>{{input.label}}</label>
          </div>
        </template>
      </div>
    </form>
  </div>
</template>
<script>
export default {
  name: 'fast-from',
  data: () => ({
    values: {}
  }),
  props: {
    needValues: {
      type: Array,
      require: true
    },
    clearTrigger: {
      type: Boolean
    }
  },
  watch: {
    values: {
      handler: function () {
        this.$emit('updateData', this.values)
      },
      deep: true
    },
    clearTrigger: function () {
      this.clear()
    }
  },
  methods: {
    clear () {
      this.values = {}
    }
  }
}
</script>
<style scoped>
</style>



