<template>
  <div id="login">
    <div class="container">
      <div class="row">
        <div class="col s12 m10 offset-m1 l8 offset-l2">
          <div class="card-panel">
            <div class="row">
              <div class="col s12 center-align">
                <i class="material-icons xxl light-green-text text-darken-2">person_pin</i>
                <h4 class="purple-text text-darken-4">Ingreso al sistema</h4>
              </div>
              <div class="col s12">
                <div class="input-field">
                  <i class="material-icons prefix">email</i>
                  <input id="email" type="text" class="validate" v-model="email">
                  <label for="email">Correo electrónico</label>
                </div>
              </div>
              <div class="col s12">
                <div class="input-field">
                  <i class="material-icons prefix">lock</i>
                  <input id="password" class="validate" v-model="password" type="password">
                  <label for="password">Contraseña</label>
                </div>
              </div>
              <div class="col s12">
                <button v-on:click="login()" v-on:keyup.enter="login()" class="waves-effect waves-light btn purple darken-4 pull right">
                  Entrar
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import axios from 'axios'
import Materialize from 'materialize-css'
export default {
  name: 'login',
  data: () => ({
    email: '',
    password: ''
  }),
  methods: {
    login () {
      var scope = this
      // this.$store.commit('increment', 'Hola')

      axios.post(scope.$store.state.wss + 'login', {
        email: this.email,
        password: this.password
      })
      .then(function (response) {
        console.log(response)
        scope.$store.commit('login', response.data)
        Materialize.toast('¡Hola ' + response.data.user.name + '!', 3000, 'rounded')
        scope.$router.push('/admin')
      })
      .catch(function (error) {
        console.log(error)
        Materialize.toast('No se pudo iniciar sesión', 3000, 'rounded')
      })
    }
  },
  mounted () {
  }
}
</script>
<style scoped>
</style>



