<template>
  <div id="paces">
    <modal-component
      v-if="$store.state.user.superUser"
      :toggle="toggle"
      :needValues="needValues"
      :okCallback="createNewPlace"
      title="Crear un lugar"
      okText="Crear"
    >
    </modal-component>
    <empty-response
      v-if="list.length === 0"
      text="No se ha creado ningún lugar"
      icon="place"
    >
    </empty-response>
    <div class="row">
      <div class="col s12 m6" v-for="place in list">
        <place-card :name="place.name" :_id="place._id" :description="place.description"></place-card>
      </div>
    </div>
    <float-btn
      v-if="$store.state.user.superUser"
      :doEvent="showModal"
      showMeTitle="Agregue un lugar"
      showMeText="Para agregar un nuevo lugar de clic aqui"
      :showMeToggle="showMeToggle"
    >
    </float-btn>
  </div>
</template>
<script>
// Import librays
import axios from 'axios'
import Materialize from 'materialize-css'

// Import my components
import PlaceCard from '@/components/UIcomponents/PlaceCard'
import FloatBtn from '@/components/UIcomponents/FloatBtn'
import ModalComponent from '@/components/UIcomponents/ModalComponent'
import EmptyResponse from '@/components/UIcomponents/EmptyResponse'

export default {
  name: 'paces',
  props: {
  },
  data: () => ({
    toggle: false,
    showMeToggle: false,
    needValues: [
      {
        type: 'text',
        label: 'Nombre',
        name: 'name'
      },
      {
        type: 'text',
        label: 'Descripción',
        name: 'description'
      }
    ],
    list: []
  }),
  components: {
    PlaceCard,
    FloatBtn,
    ModalComponent,
    EmptyResponse
  },
  methods: {
    showModal () {
      this.toggle = !this.toggle
    },
    showAditional () {
      this.showMeToggle = !this.showMeToggle
    },
    pushPlace (place) {
      this.list.push(place)
    },
    createNewPlace (data) {
      console.log('Creando un nuevo lugar ...')
      console.log(data)
      var scope = this
      axios.post(scope.$store.state.wss + 'place' + '?token=' + scope.$store.state.token,
        {
          name: data.name,
          description: data.description
        }
      )
      .then(function (response) {
        console.log(response)
        Materialize.toast('¡Se ha agregado un el nuevo lugar!', 1500, 'rounded')
        var place = {
          name: response.data.name,
          description: response.data.description,
          _id: response.data._id
        }
        scope.pushPlace(place)
      })
      .catch(function (error) {
        console.log(error)
        Materialize.toast('No se pudo iniciar sesión', 3000, 'rounded')
      })
    }
  },
  mounted () {
    var scope = this
    axios.get(scope.$store.state.wss + 'place' + '?token=' + scope.$store.state.token)
    .then(function (response) {
      console.log(response)
      scope.list = response.data
      if (scope.list.length === 0) {
        scope.showAditional()
      }
    })
    .catch(function (error) {
      console.log(error)
    })
  }
}
</script>



