<template>
  <div id="card-icon">
    <div class="card">
      <div class="card-content row">
        <div class="">
          <div class="col s3">
            <i class="material-icons xxl" :class="data.color">{{data.icon}}</i>
          </div>
          <div class="col s9 right-align">
            <div class="col s12">
              {{data.title}}
            </div>
            <div class="col s12 xl">
              {{data.value}}
            </div>
          </div>
        </div>
      </div>
      <div class="card-action">
        <span class="grey-text">
          <i class="material-icons left">{{data.action.icon}}</i>
          {{data.action.title}}
        </span>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: 'card-icon',
  data: () => ({

  }),
  props: {
    data: {
      type: Object,
      required: true
    }
  }
}
</script>
<style scoped>
  .row{
    margin-bottom: 0px;
  }

  .card-content{
    padding: 15px;
  }
</style>



