<template>
  <div id="menu">
    <nav class="top-nav purple darken-4">
      <div class="container">
        <div class="nav-wrapper">
          <a href="#" data-activates="slide-out" class="button-collapse top-nav full hide-on-large-only"><i class="material-icons">menu</i></a>
          <a class="brand-logo">
            {{$route.name}}
          </a>
        </div>
        <ul class="right">
          <li>
            <a class="dropdown-button" :data-activates="id">
              <span class="hide-on-med-and-up">
                <i class="material-icons">notifications</i>
              </span>
              <span class="hide-on-small-only">
                <i class="material-icons left">notifications</i>
                Notificaciones
                <span class="new badge light-green darken-2" data-badge-caption="nuevas">4</span>
                <i class="material-icons right">arrow_drop_down</i>
              </span>
            </a>
          </li>
        </ul>
        <ul :id="id" class="dropdown-content collection" style="width: 400px; max-width: 90%">
          <li class="collection-item avatar">
            <img src="../../assets/img/diego.jpg" alt="" class="circle">
            <span class="title">Title</span>
            <p>First Line <br>
              Second Line
            </p>
            <a href="#!" class="secondary-content"><i class="material-icons">grade</i></a>
          </li>
          <li class="collection-item avatar">
            <i class="material-icons circle">folder</i>
            <span class="title">Title</span>
            <p>First Line <br>
              Second Line
            </p>
            <a href="#!" class="secondary-content"><i class="material-icons">grade</i></a>
          </li>
          <li class="collection-item avatar">
            <i class="material-icons circle green">insert_chart</i>
            <span class="title">Title</span>
            <p>First Line <br>
              Second Line
            </p>
            <a href="#!" class="secondary-content"><i class="material-icons">grade</i></a>
          </li>
          <li class="collection-item avatar">
            <i class="material-icons circle red">play_arrow</i>
            <span class="title">Title</span>
            <p>First Line <br>
              Second Line
            </p>
            <a href="#!" class="secondary-content"><i class="material-icons">grade</i></a>
          </li>
        </ul>
      </div>
    </nav>
    <ul id="slide-out" class="side-nav fixed" style="transform: translateX(0px);">
      <li><div class="user-view">
        <div class="background" v-bind:style="{'background': color}">
          <img src="../../assets/img/icon-seamless_ef568d79-394b-49ab-a3c5-128827d788e8.png">
        </div>
        <a href="#!user"><img class="circle" src="../../assets/img/diego.jpg"></a>
        <span v-if="$store.state.user.superUser" data-badge-caption="Admin" class="new badge white purple-text text-darken-4"></span>
        <a href="#!name"><span class="white-text name">
          {{$store.state.user.name}} {{$store.state.user.lastname}}
        </span></a>
        <a href="#!email"><span class="white-text email">
          {{$store.state.user.email}}
        </span></a>
      </div></li>
      <template v-for="link in links">
        <li v-if="link.indexable">
          <router-link :to="{ name: link.name}">
            <i class="material-icons">{{link.icon}}</i>
            {{link.name}}
          </router-link>
        </li>
      </template>
      <li><div class="divider"></div></li>
      <li>
        <a v-on:click="logout()" class="clickable">
          <i class="material-icons">exit_to_app</i>
          Cerrar sesión
        </a>
      </li>
      <template v-if="$store.state.user.superUser">
        <li><div class="divider"></div></li>
        <li><a class="subheader">Opciones de administrador</a></li>
        <li>
          <a href="#!">
            <i class="material-icons">person</i>
            Administrar usuaros
          </a>
        </li>
      </template>
    </ul>
  </div>
</template>
<script>
var $ = require('jquery')
export default {
  name: 'menu',
  data: () => ({
    links: [],
    id: 0
  }),
  props: ['color'],
  mounted () {
    this.links = this.$router.options.routes[0].children
    this.id = this._uid
    $(document).ready(function () {
      $('.button-collapse').sideNav()
      $('.dropdown-button').dropdown({
        belowOrigin: true,
        constrainWidth: false
      })
    })
  },
  methods: {
    logout () {
      this.$store.commit('logout')
    }
  }
}
</script>
<style scoped>
  .background>img{
    -webkit-filter: opacity(0.2);
  filter: opacity(0.2);
  }
</style>



