<template>
  <div class="float-btn" v-on:click="doEvent()">
    <div class="fixed-action-btn">
      <a :id="id" class="btn-floating btn-large red" :class="{'waves-effect waves-light': !complex}">
        <i class="large material-icons">{{icon}}</i>
      </a>
      <ul v-if="complex">
        <li><a class="btn-floating red"><i class="material-icons">insert_chart</i></a></li>
        <li><a class="btn-floating yellow darken-1"><i class="material-icons">format_quote</i></a></li>
        <li><a class="btn-floating green"><i class="material-icons">publish</i></a></li>
        <li><a class="btn-floating blue"><i class="material-icons">attach_file</i></a></li>
      </ul>
    </div>
    <div class="tap-target" :data-activates="id">
      <div class="tap-target-content white-text">
        <h5>{{showMeTitle}}</h5>
        <p>{{showMeText}}</p>
      </div>
    </div>
  </div>
</template>
<script>
var $ = require('jquery')
export default {
  name: 'float-btn',
  data: () => ({
    id: null
  }),
  props: {
    color: {
      type: String,
      default: 'red'
    },
    icon: {
      type: String,
      default: 'add'
    },
    complex: {
      type: Boolean,
      default: false
    },
    doEvent: {
      type: Function,
      default: function () {
        console.log('Defaul doEvent')
      }
    },
    showMeToggle: {
      type: Boolean
    },
    showMeTitle: {
      type: String,
      default: 'Nuevo elemento'
    },
    showMeText: {
      type: String,
      default: 'Para agregar un nuevo elemento haga clic aquí'
    }
  },
  watch: {
    showMeToggle: function () {
      console.log('I am FloatBtn and a execute jQuery')
      $('.tap-target').tapTarget('open')
    }
  },
  methods: {
    makeid () {
      var text = ''
      var possible = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789'

      for (var i = 0; i < 15; i++) {
        text += possible.charAt(Math.floor(Math.random() * possible.length))
      }
      return text
    }
  },
  mounted () {
    this.id = this.makeid()
  }
}
</script>
<style scoped>
.fixed-action-btn{
  padding-top: 0;
  border-radius: 50%;
}
</style>



