<template>
  <div class="pace">
    <modal-component
      v-if="$store.state.user.superUser"
      :toggle="modalToggle"
      :needValues="modalNeedValues"
      :okCallback="createNewPlace"
      title="Crear un dispositivo"
      okText="Crear"
    >
    </modal-component>
    <div class="row container" v-if="place != null">
      <div class="col s12">
        <div class="col s12">
          <div class="card-panel light-green darken-2 white-text">
            <h4>{{place.name}}</h4>
            <p>{{place.description}}</p>
          </div>
        </div>
      </div>
    </div>
    <div class="row">
      <div class="col s12">
        <empty-response
          v-if="place == null || place.devices == null || place.devices.length === 0"
          text="No hay dispositivos para mostrar"
          icon="devices"
        >
        </empty-response>
        <div v-else>
          <div class="col m6 l4" v-for="device in place.devices">
            <device-card :device="device" class="col s12"></device-card>
          </div>
        </div>
      </div>
    </div>

    <float-btn
      v-if="$store.state.user.superUser"
      showMeTitle="Agregue un dispositivo"
      showMeText="Para agregar un nuevo dispositivo de clic aqui"
      :showMeToggle="btnExtraInfoToggle"
      :doEvent="openModal"
    >
    </float-btn>
  </div>
</template>
<script>
import axios from 'axios'

import FloatBtn from '@/components/UIcomponents/FloatBtn'
import EmptyResponse from '@/components/UIcomponents/EmptyResponse'
import DeviceCard from '@/components/UIcomponents/DeviceCard'
import ModalComponent from '@/components/UIcomponents/ModalComponent'

export default {
  name: 'pace',
  data: () => ({
    place: null,
    btnExtraInfoToggle: false,
    modalToggle: false,
    modalNeedValues: [
      {
        type: 'text',
        label: 'Nombre del dispositivo',
        name: 'name'
      }
    ]
  }),
  methods: {
    showAditional () {
      this.btnExtraInfoToggle = !this.btnExtraInfoToggle
    },
    openModal () {
      this.modalToggle = !this.modalToggle
    },
    createNewPlace (data) {
      console.log(data)
      var scope = this
      axios.post(scope.$store.state.wss + 'device' + '?token=' + scope.$store.state.token,
        {
          name: data.name,
          placeId: scope.$route.params.name
        }
      )
      .then(function (response) {
        scope.place.devices.push(response.data)
        console.log(response)
      })
      .catch(function (error) {
        console.log(error)
      })
    }
  },
  mounted () {
    var scope = this
    axios.get(scope.$store.state.wss + 'place/' + scope.$route.params.name + '?token=' + scope.$store.state.token)
    .then(function (response) {
      console.log(response)
      scope.place = response.data
      // scope.place.devices.push({name: 'Televisor', id: 'asdasd'})
      // scope.place.devices.push({name: 'Aire acondicionado', id: '2453453'})
      if (scope.place.devices.length === 0) {
        console.log('I need change')
        scope.showAditional()
      }
    })
    .catch(function (error) {
      console.log(error)
    })
  },
  components: {
    FloatBtn,
    EmptyResponse,
    ModalComponent,
    DeviceCard
  }
}
</script>
<style scoped>
</style>



