import Vue from 'vue'
import Router from 'vue-router'

// Imoprt my components
import Welcome from '@/components/tempate/Welcome'
import Index from '@/components/tempate/Index'
import Login from '@/components/tempate/Login'
import Dashboard from '@/components/tempate/Dashboard'
import Places from '@/components/tempate/Places'
import Place from '@/components/tempate/Place'
import Admin from '@/components/tempate/Admin'
import Config from '@/components/tempate/Config'
import Device from '@/components/tempate/Device'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/admin',
      name: 'Admin',
      component: Admin,
      redirect: '/admin/panel',
      beforeEnter: (to, from, next) => {
        if (localStorage.getItem('user') !== null && localStorage.getItem('user') !== '') {
          next()
        } else {
          next('/')
        }
      },
      children: [
        {
          path: 'panel',
          name: 'Panel',
          component: Dashboard,
          icon: 'dashboard',
          indexable: true,
          props: {
            superUser: true
          }
        },
        {
          path: 'lugares',
          name: 'Mis lugares',
          component: Places,
          icon: 'place',
          indexable: true,
          props: {
            superUser: true
          }
        },
        {
          path: 'lugar/:name',
          name: 'Lugar',
          component: Place,
          indexable: false,
          props: {
            superUser: true
          }
        },
        {
          path: 'configuracion',
          name: 'Configuracion',
          component: Config,
          icon: 'settings',
          indexable: true,
          props: {
            superUser: true
          }
        },
        {
          path: 'dispositivo/:id',
          name: 'Dispositivo',
          component: Device,
          icon: 'devices',
          indexable: false,
          props: {
            superUser: true
          }
        }
      ]
    },
    {
      path: '/bienvenido',
      component: Welcome,
      indexable: false,
      redirect: '/bienvenido/inicio',
      beforeEnter: (to, from, next) => {
        if (localStorage.getItem('user') !== null && localStorage.getItem('user') !== '') {
          next('/admin')
        } else {
          next()
        }
      },
      children: [
        {
          path: 'inicio',
          name: 'Inicio',
          component: Index,
          icon: 'home',
          indexable: true
        },
        {
          path: 'login',
          name: 'Ingresar',
          component: Login,
          icon: 'person',
          indexable: true
        }
      ]
    },
    {
      path: '/',
      redirect: '/bienvenido'
    },
    {
      path: '*',
      redirect: '/'
    }
  ]
})



